export default {
  methods: {
    change(e) {
      this.$parent.addInputFile(e.target)
      e.target.value = ''
      if (!e.target.files) {
        // ie9 fix #219
        this.$destroy()
        // eslint-disable-next-line
        new this.constructor({
          parent: this.$parent,
          el: this.$el,
        })
      }
    }
  }
}
